body {
  background-color: #fafcfb;
  font-family: Josefin Sans, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.header__section {
  border-bottom: 2px solid #4e4e4e;
  border-radius: 5px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.logo {
  letter-spacing: .03em;
  color: #2196f3;
  flex-direction: column;
  align-items: center;
  font-family: titan one, sans-serif;
  font-size: 42px;
  line-height: 1.1;
  display: flex;
}

.logo__box {
  align-items: center;
  display: flex;
}

.logo__icon {
  fill: #2196f3;
}

.logo-span {
  color: #1ebb92;
  font-size: 20px;
}

@media screen and (max-width: 1199px) {
  .nav-menu, .header__phone-menu, .header-social {
    display: none;
  }

  .mobile-btn {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    padding: 12px 8px;
    display: block;
  }

  .mobile-btn__icon {
    fill: #4e4e4e;
    display: block;
    position: fixed;
    top: 20px;
    right: 20px;
  }

  .mobile-btn:focus .mobile-btn__icon, .mobile-btn:hover .mobile-btn__icon {
    fill: #1ebb92;
  }
}

@media screen and (min-width: 1200px) {
  .nav-menu {
    gap: 15px;
    display: flex;
  }

  .nav-menu__link {
    letter-spacing: .01em;
    color: #4e4e4e;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.2;
  }

  .nav-menu__link--active, .nav-menu__link:hover, .nav-menu__link:focus {
    color: #1ebb92;
  }

  .header__phone-menu {
    flex-direction: column;
    gap: 10px;
    display: flex;
  }

  .phone-menu__link {
    letter-spacing: .03em;
    color: #2196f3;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.2;
  }

  .phone-menu__link:hover, .phone-menu__link:focus {
    color: #1ebb92;
  }

  .header-social {
    gap: 15px;
    display: flex;
  }

  .mobile-btn {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .mobile-menu {
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background-color: #dcfff9;
    transition: opacity .25s linear, visibility .25s linear;
    position: fixed;
    top: 0;
    left: 0;
    overflow: scroll;
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .mobile-menu__container {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px;
    transition: transform .25s linear;
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(.5);
  }

  .is-open .mobile-menu__container {
    transform: translate(-50%, -50%)scale(1);
  }

  .mobile-menu__close-btn {
    width: 40px;
    height: 40px;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    justify-content: center;
    align-items: center;
    padding: 0;
    display: flex;
    position: absolute;
    top: 10px;
    right: 15px;
  }

  .mobile-menu__close-icon {
    fill: #4e4e4e;
    transition: fill .25s linear;
  }

  .mobile-menu__close-btn:focus .mobile-menu__close-icon, .mobile-menu__close-btn:hover .mobile-menu__close-icon {
    fill: #1ebb92;
  }

  .mobile-nav__item {
    margin-top: 30px;
  }

  .mobile-nav__link {
    color: #4e4e4e;
    letter-spacing: .02em;
    font-size: 24px;
    font-weight: 700;
    line-height: 1.2;
    transition: color .25s linear;
  }

  .mobile-nav__link:hover, .mobile-nav__link:focus, .mobile-nav__link--active {
    color: #1ebb92;
  }

  .mobile-contact__item:not(:last-child) {
    margin-bottom: 20px;
  }

  .mobile-contact__link {
    letter-spacing: .02em;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.2;
    transition: color .25s linear;
  }

  .mobile-contact__link--phone {
    color: #2196f3;
  }

  .mobile-contact__link--mail {
    color: #4e4e4e;
  }

  .mobile-contact__link:hover, .mobile-contact__link:focus {
    color: #1ebb92;
  }

  .mobile-social {
    gap: 20px;
    margin-top: 50px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-menu {
    display: none;
  }
}

