.uslugi-hero__section {
	background-color: #ffffff;
	border-bottom: 2px solid #4e4e4e;
	border-radius: 5px;
}
.uslugi-hero__container {
	position: relative;
	text-align: end;
	padding-top: 35px;
	padding-bottom: 35px;
}
.back-to-service {
	display: inline-block;
	text-align: center;
	margin-bottom: 30px;
	width: 200px;
	margin-right: 0;
	font-weight: 700;
	font-size: 18px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	cursor: pointer;
	color: #4e4e4e;
	background-color: #dcfff9;
	border: none;
	border-radius: 10px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
	transition: background-color 250ms linear;
	&:hover,
	&:focus {
		color: #dcfff9;
		background-color: #1ebb92;
	}
}
.uslugi-hero__title {
	padding-left: 100px;
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 28px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #2196f3;
}
.uslugi-hero__text {
	padding-left: 100px;
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 18px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #4e4e4e;
}
.uslugi-hero__btn {
	padding: 10px 30px;
	font-weight: 700;
	font-size: 24px;
	line-height: 1.2;
	letter-spacing: 0.03em;
	cursor: pointer;
	color: #4e4e4e;
	background-color: #dcfff9;
	border: none;
	border-radius: 10px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
	transition: background-color 250ms linear;
	&:hover,
	&:focus {
		color: #dcfff9;
		background-color: #1ebb92;
	}
}
.doctor__img--uslugi {
	top: 30px;
}
.uslugi-main__section {
	border-bottom: 2px solid #4e4e4e;
	border-radius: 5px;
}
.uslugi-main__container {
	position: relative;
	padding-top: 30px;
	padding-bottom: 30px;
	text-align: justify;

	@media screen and (min-width: 768px) {
		padding-right: 100px;
	}
	@media screen and (min-width: 1200px) {
		padding-right: 300px;
	}
}
.uslugi-main__title {
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 28px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #2196f3;
}
.uslugi-main__subtitle {
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 22px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #2196f3;
}
.uslugi-main__text {
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 18px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #4e4e4e;
}
.med-menu {
	display: flex;
	flex-direction: column;
	gap: 15px;
	margin-bottom: 20px;
}
.med-menu__item {
	display: flex;
	gap: 30px;
	font-weight: 700;
	font-size: 20px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #4e4e4e;
}
.med-menu__title {
	margin-bottom: 20px;
	font-weight: 700;
	font-size: 20px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #4e4e4e;
}
.med-menu__text {
	font-weight: 700;
	font-size: 22px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	color: #2196f3;
}
.spritz-icon {
	@media screen and (min-width: 1200px) {
		position: absolute;
		bottom: 50px;
		right: -50px;
	}
}
.uslugi-price__link {
	font-weight: 700;
	font-size: 22px;
	line-height: 1.2;
	letter-spacing: 0.03em;
	color: #e4510d;
	&:hover,
	&:focus {
		color: #1ebb92;
	}
}
